import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.rtl.min.css';
import { Container, Nav, Navbar } from 'react-bootstrap';
import axios from 'axios';
import Paper from '@mui/material/Paper';
import { Link } from 'react-router-dom';
import IconButton from '@mui/material/IconButton';
import { Contacts, Facebook, Home, Phone, Twitter, WhatsApp, ContactSupport } from '@mui/icons-material';
import { Box, Skeleton, Stack, Tooltip } from '@mui/material';
import { Chip } from '@mui/material';
import Typography from '@mui/material/Typography';
import '../nav.css';

class AppNavbar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            siteLogo: {},
            contactInfo: {},
            isLoaded: false
        }
    }

    componentDidMount() {
        this.logoData();
        this.contactInfoData();
    }

    logoData = () => {
        axios.get('/api/sitelogo/').then((res) => {
            this.setState({ siteLogo: res.data });
        }).catch((err) => console.log(err));
    };

    contactInfoData = () => {
        axios.get('/api/contactinfo/').then((res) => {
            this.setState({ isLoaded: true, contactInfo: res.data });
        }).catch((err) => console.log(err));
    };

    render() {
        const { siteLogo, contactInfo, isLoaded } = this.state;
        return (
            <Paper component='nav' className='navbar-container'>
                <Navbar collapseOnSelect expand='lg' bg='white' variant='light' fixed='top'>
                    <Container fluid={true}>
                        <Navbar.Brand href='#'>
                            <img
                                alt=''
                                src={siteLogo['logo']}
                                width='30'
                                height='30'
                                className='d-inline-block align-top' />{' '}
                            <Typography variant='span' component='span' className={'fw-bold fs-5'} color='primary'>
                                {siteLogo['name']}
                            </Typography>
                        </Navbar.Brand>
                        <Navbar.Toggle aria-controls='responsive-navbar-nav' />
                        <Navbar.Collapse id='responsive-navbar-nav'>
                            <Nav className='me-auto'>
                                <Stack direction={'row'}>
                                    <Tooltip title='Home' arrow>
                                        <Chip icon={<Home />} label="الرئيسية" component={'a'} clickable as={Link} to='/' className='p-3 me-2 mt-1' />
                                    </Tooltip>
                                    <Tooltip title='About Us' arrow>
                                        <Chip icon={<Contacts />} label="عنا" component={'a'} clickable as={Link} to='/about' className='p-3 me-2 mt-1' />
                                    </Tooltip>
                                    <Tooltip title='Contact Us' arrow>
                                        <Chip icon={<ContactSupport />} label="تواصل معنا" component={'a'} clickable as={Link} to='/contact' className='p-3 me-2 mt-1' />
                                    </Tooltip>
                                </Stack>
                            </Nav>
                            {!isLoaded ? (
                                <Stack direction={'row'}>
                                    {
                                        Array.from(new Array(4)).map((el, i) =>

                                            <Box sx={{ pt: 0.5 }} className={'ms-2'} key={i}>
                                                <Skeleton variant='rectangular' style={{ borderRadius: '8px' }} width={35}
                                                    height={35} />
                                            </Box>
                                        )}
                                </Stack>
                            ) : (
                                <Box sx={{ pt: 0.5 }}>
                                    {contactInfo['facebook'] ? (
                                        <IconButton color='primary' href={contactInfo['facebook']} target={'_blank'}
                                            aria-label='Facebook'
                                            className={'ms-2 btn-nav-contact'}>
                                            <Facebook />
                                        </IconButton>
                                    ) : ''}
                                    {contactInfo['twitter'] ? (
                                        <IconButton color='primary' href={contactInfo['twitter']} target={'_blank'}
                                            aria-label='Twitter' className={'ms-2 btn-nav-contact'}>
                                            <Twitter />
                                        </IconButton>
                                    ) : ''}
                                    {contactInfo['whatsapp'] ? (
                                        <IconButton color='primary' href={contactInfo['whatsapp']} target={'_blank'}
                                            aria-label='Whatsapp' className={'ms-2 btn-nav-contact'}>
                                            <WhatsApp />
                                        </IconButton>
                                    ) : ''}
                                    {contactInfo['phone'] ? (
                                        <IconButton color='primary' href={'tel:' + contactInfo['phone']}
                                            aria-label='Phone' className={'ms-2 btn-nav-contact'}>
                                            <Phone />
                                        </IconButton>
                                    ) : ''}
                                </Box>
                            )
                            }
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
            </Paper>
        );
    }
}

export default AppNavbar;
